<template>
  <div :style="bgStyle">
    тут какой-то текст
  </div>
</template>

<script>
export default {
  data() {
    return {
      tg: window.Telegram.WebApp,
      bgStyle: {
        // Устанавливаем свои цвета
        backgroundColor: '#ff0000' // Пример: красный цвет фона
      },
    };
  },
  beforeMount() {
    this.tg.expand();
    this.tg.enableClosingConfirmation();
    this.setCustomColors();
  },
  methods: {
    setCustomColors() {
      // Переопределение стандартных цветов на пользовательские
      document.documentElement.style.setProperty('--tg-theme-bg-color', '#ff0000'); // Красный
      document.documentElement.style.setProperty('--tg-theme-secondary-bg-color', '#00ff00'); // Зеленый
      document.documentElement.style.setProperty('--tg-theme-button-color', '#0000ff'); // Синий, как пример
      // Добавь другие переменные по необходимости
    },
  },
};
</script>

<style scoped>
/* Пример использования переопределенных переменных */
div {
  border: 1px solid var(--tg-theme-secondary-bg-color);
}
</style>
